/* 사용자 BOM 입력 Step 1 전체 */

.bom-user-step1-all{
    margin-left:200px;
    margin-right:200px;
}


/* 사용자 BOM 입력 헤더 */

.bom-user-header-div{
    margin-top:30px;
}

.bom-user-header-flex{
    display:flex;
    float:left;
}

.bom-user-header-logout{
    display: flex;
    justify-content: flex-end;
    font-size:18px;
    margin-right:50px;
}

 .bom-user-header-link{
    margin-left: 150px;
    font-weight: bold;
    font-size:18px;
} 



/* 사용자 BOM 입력 상단 바 */

.bom-user-status-bar{
    border-bottom:1px solid black;
    font-size:20px;
    text-align: center;
    margin-top:40px;
}

/* 사용자 BOM 입력 메인 컨텐츠 */

.bom-user-input1-main-flex{
    display: flex;
    height:800px;
    margin-top:50px;
    margin-right: auto;
    margin-left: auto;
}


/* 사용자 BOM 입력-1 사이드 바 */

.bom-user-input1-select-bar{
    text-align: center;
    text-align-last: center;
    display: flex;
}

.bom-user-input1-select-table-wrap {
    display: table;
}

.bom-user-input1-select-box{
    margin-bottom: 20px;
}

.bom-user-input1-button_box {
    display: flex;
    border: 1px solid black;
    margin-bottom: 5px;
}

.bom-user-input1-button_box > button {
    width: 130px;
    height:40px;
    background-color: #fbfbfb;
    border:none;
}

.bom-user-input1-button_box > button > span {
    margin-left:15px;
}


.bom-user-input1-button_box img {
    height: 40px;
    width:20px;
}

.bom-user-input1-select-box > p{
    display: table-cell;
    border: 1px solid black;
    background-color: #e9e9FF;
    height:48px;
    width:152px;
    vertical-align: middle;
    font-size : 12px;
}


.bom-user-input1-select-view-div{
    width: 180px;
    margin-left:5px;
    overflow: auto;
}

.bom-user-input1-select-view-box{
    width: 150px;
    height:48px;
    border: 1px groove #e9e9FF;
    background-color: #f5f5ff;
    margin-bottom: 2px;
    font-size : 12px;
    display: table;
}

.bom-user-input1-select-view-box div {
    display: table-cell;
    vertical-align: middle;
}

.margin2{
    margin-top:115px;
}

.margin3{
    margin-top:230px;
}

.margin4{
    margin-top:345px;
}

.margin5{
    margin-top:460px;
}

.margin6{
    display:flex;
    flex-direction: column-reverse;
    margin-bottom:182px;
}

.margin7{
    display:flex;
    flex-direction: column-reverse;
    margin-bottom:67px;
}



/* 사용자 BOM 입력-1 폼-1 */

.bom-user-input-form{
    margin-left: 10px;
    width:1000px;
    margin-bottom: 30px;
}

.bom-user-input-form-fieldset{
    border: 2px groove black ;
    padding: 30px; 
    border-radius: 15px;
    height:270px;
}

.bom-user-input-form-legend{
    width:auto;
    margin-left: 20px;
    margin-bottom: 0;
    border-bottom: none;
}

.bom-user-input-form1-pre-div{
    height: 100px;
}

.bom-user-input-form1-pre-div > p {
    height: 60px;
    padding: 9.5px;
    margin: 0 0 10px;
    font-size: 13px;
    line-height: 1.42857143;
    color: #333;
    background-color: #f5f5f5;
    border: 1px solid #ccc;
    border-radius: 4px;
    word-wrap: break-word;
}

.bom-user-input-form1-bomcd {
    margin-bottom: 30px;
    font-weight: bold;
}

.bom-user-input-form1-bomcd > span {
    margin-right:30px;
}

.bom-user-input-form1-bomcd > select {
    text-align-last: center;
    width:200px;
    height:30px;
}

.bom-user-input-form1-text input{
    width:700px;
    height:30px;
    margin-left:50px;
}


.bom-user-input-form1:nth-child(2){
    margin-top:40px;
}




/* 사용자 BOM 입력-1 폼-2 */


.bom-user-input-form2{
    margin-top:70px;
    text-align: center;
}

.bom-user-input-form2-header{
    display: flex;
    
}

.bom-user-input-form2-header p{
    width:67px;
    border:1px solid black;
    margin-right: 5px;
    line-height: 30px;
    background-color: #e9e9FF;
    height:32px;

}

.bom-user-input-form2-header p:first-child, .bom-user-input-form2-header p:last-child{
    width:97px;
}

.bom-user-input-form2-header p:first-child {
    margin-top : 8px;
}

.bom-user-input-form2-count{
    display: flex;
}

.bom-user-input-form2-count p{
    width:67px;
    border:1px solid black;
    margin-right: 5px;
    line-height: 30px;
    height:32px;
}

.bom-user-input-form2-count p:first-child, .bom-user-input-form2-count p:last-child{
    width:97px;
}

.bom-user-input-form2-count p:first-child {
    margin-top : 8px;
}


/* 사용자 BOM 입력-2 가는 버튼 */

.bom-user-input-arrow{
    border:1px solid #000000;
    background-color: #F0F0FF;
    width: 100px;
    line-height: 100px;
    text-align: center;
}

.bom-user-input-arrow-box{
    display: flex;
    align-items: center;
    margin-left:50px;
    margin-bottom:130px;
    font-size: 22px;
}