/* 관리자 PBS 리스트 전체 */

.admin-bom-pbs-all{
    margin-left:200px;
    margin-right:200px;
}


/* 관리자 BOM 헤더 */

.admin-bom-pbs-header-div{
    margin-top:30px;
}

.admin-bom-pbs-header-flex{
    display:flex;
    float:left;
}

.admin-bom-pbs-header-logout{
    display: flex;
    justify-content: flex-end;
    font-size:18px;
    margin-right:50px;
}

 .admin-bom-pbs-header-link{
    margin-left: 150px;
    font-weight: bold;
    font-size:18px;
} 



/* 관리자 PBS 리스트 상단 바 */

.admin-user-title{
    margin-top: 40px;
    padding-top: 5px;
    width: 100%;
    height: 40px;
    border-bottom : 2px solid #b0ccd4; 
    text-align: center;
}

/* 관리자 PBS List 컨텐츠 */

.admin-bom-pbs-content-div {
    display: flex;
}

/* 관리자 PBS 리스트 Nav */

.admin-home-nav{
    float: left;
    margin-left: 70px;
}

.admin-manager-nav-menu-selected {
    background-color: #b0ccd4;
}

.admin-nav-menu-p{
    width: 200px;
    height: 40px;
    border: 1px solid #b0ccd4;
    text-align: center;
    padding-top: 7px;
    margin-top: 15px;
    color: #324960;
    cursor: pointer;
}

.admin-nav-menu > p:hover {
    background-color: #b0ccd4;
    cursor: pointer;
}




/* pbs 트리 테이블 전체 css */

.admin-bom-pbs-table-div {
    font-size: 13px;
}

.admin-bom-pbs-table-div .rs-table-row-header .rs-table-cell {
    background: #f0f0ff;
}

.admin-bom-pbs-table-div .rs-table-hover .rs-table-row:not(.rs-table-row-header):hover,
.admin-bom-pbs-table-div .rs-table-hover .rs-table-row:hover .rs-table-cell-group,
.admin-bom-pbs-table-div .rs-table-hover .rs-table-row:hover .rs-table-cell {
  background: #f0f0f0;
}

.admin-bom-pbs-table-div .rs-table-word-wrap .rs-table-cell-content {
    white-space: normal;
    word-break:keep-all;
  }


/* 레벨에 따른 들여쓰기 10으로 강제 고정 */
.admin-bom-pbs-table-div .rs-table-cell-content {
    padding-left: 10px !important;
}



/* PBS List Table Css */


.admin-bom-pbs-table-div .admin-bom-pbs-table {
    width: 750px;
    height: 800px;
    margin-left: 120px;
    margin-top : 50px;
}

.admin-bom-pbs-table-div div[data-depth='0'] .pbs-row-1 {
    background-color: #d6d1fd;
}

.admin-bom-pbs-table-div div[data-depth='1'] .pbs-row-1 {
    background-color:#dceaf7;
}

.admin-bom-pbs-table-div div[data-depth='2'] .pbs-row-1 {
    background-color: rgb(186, 232, 253);
}


.admin-bom-pbs-table-div div[data-depth='0'] .pbs-row-2 {
    background-color: #d6d1fd;
}

.admin-bom-pbs-table-div div[data-depth='1'] .pbs-row-3 {
    background-color:#dceaf7;
}

.admin-bom-pbs-table-div div[data-depth='2'] .pbs-row-4 {
    background-color: rgb(186, 232, 253);
}


.admin-bom-pbs-table-div div[role='treegrid']{
    text-align: center;
}

.admin-bom-pbs-table-div div[role='gridcell']:first-child{
    text-align: left;
}

.admin-bom-pbs-table-div .admin-bom-pbs-table .rs-table-cell-header .rs-table-cell-content{
    font-weight: bold;
    font-size:14px;
    color:#7777D9;
}

.admin-bom-pbs-table-div .rs-table-cell-expand-wrapper .pbs-button {
    display: none;
}

.admin-bom-pbs-table-div div[data-depth='0'] .pbs-row-2 .pbs-button {
    display: block;
}

.admin-bom-pbs-table-div div[data-depth='1'] .pbs-row-3 .pbs-button {
    display: block;
}



