/* 사용자 BOM 입력 Step 1 전체 */

.bom-user-input-step1-contain {
    margin-left:200px;
    margin-right:200px;
}

/* 사용자 BOM 입력 상단 바 */

.bom-user-input-status-bar {
    margin-top: 20px;
    margin-bottom: 30px;
    padding-top: 5px;
    width: 100%;
    height: 40px;
    border-bottom : 2px solid #b0ccd4; 
    text-align: center;
}



/* 사용자 BOM 입력 메인 컨텐츠 */

.bom-user-input-step1-main-wrap {
    width: 1000px;
    margin: 0 auto;
    padding-left:90px;
}



/* BOM 사용자 입력 장치 level 바 */

.bom-user-input-device-bar-fieldset {
    border: 2px groove black ;
    border-radius: 15px;
    padding:15px 30px 30px 20px;
    height:130px;
    width: 800px;
}

.bom-user-input-device-bar-fieldset legend {
    width:auto;
    margin-left: 20px;
    margin-bottom: 0;
    border-bottom: none;
    padding:0 20px;
}

.bom-user-input-device-bar-fieldset p {
    height: 60px;
    padding: 9.5px;
    margin: 0 0 10px;
    font-size: 13px;
    color: #333;
    background-color: #f5f5f5;
    border: 1px solid #ccc;
    border-radius: 4px;
    word-wrap: break-word;
}




/* BOM 사용자 입력 칸 */

.bom-user-input-step1-input-div-wrap {
    border: 1px solid black;
    padding: 10px;
    padding-left:20px;
    margin-top: 30px;
    width: 800px;
    
}


.bom-user-input-step1-input-box {
    margin-bottom: 10px;
}



.bom-user-input-step1-input-box select {
    text-align-last: center;
    width:200px;
    height:30px;
}

.bom-user-input-step1-input-box input{
    width:746px;
    height:30px;
}


/* 수량 입력 칸 */


.bom-user-input-step1-count-box-header p {
    width:63px;
    border:1px solid black;
    line-height: 30px;
    background-color: #e9e9FF;
    height:32px;
    margin: 0;
    margin-left:5px;
    text-align: center;
}


.bom-user-input-step1-count-box-bottom input, 
.bom-user-input-step1-count-box-bottom p{
    width:63px;
    border:1px solid black;
    line-height: 30px;
    height:32px;
    margin-top:5px;
    margin-left:5px;
    text-align: center;
}

.bom-user-input-step1-count-header {
    border-top:1px solid rgb(143, 192, 201);
    margin-bottom: 10px;
    margin-top:20px;
    width:746px;
}

.bom-user-input-step1-count-header legend {
    border: none;
    width: auto;
    margin:0 auto;
    padding:0 30px;
    color : rgb(177, 169, 153);
}



/* 사용자 BOM 입력-2 가는 버튼 */


.bom-user-input-step1-link-wrap {
    display: flex;
    margin-left:100px;
    margin-top: 200px;
    font-size: 22px;
}

.bom-user-input-step1-link {
    border:1px solid #000000;
    background-color: #F0F0FF;
    width: 100px;
    line-height: 100px;
    text-align: center;
}


/* 스텝 2 */

.bom-user-input-step2-first-textarea {
    width:746px;
    height: 100px;
    resize: none;
}

.bom-user-input-step2-second-textarea {
    width:746px;
    height: 60px;
    resize: none;
}



/* 사용자 BOM 입력 스텝1 가는 버튼 */

.bom-user-input-step2-link{
    border:1px solid #000000;
    background-color: #F0F0FF;
    width: 100px;
    line-height: 100px;
    text-align: center;
}

.bom-user-input-step2-link-wrap{
    display: flex;
    margin-left:-200px;
    margin-top:200px;
    font-size: 22px;
}


/* 스텝 2 서브밋 버튼 */

.bom-user-input-step2-button-wrap {
    margin-left: 320px;
    margin-top : 30px;
}

.bom-user-input-step2-button-wrap button {
    width:80px;
    height:40px;
    margin-right:50px;
    background-color: #fdfdff;
    border-radius: 10px;
    border: 2px groove #8f8f8f;
}


/* /////////////////////////////////// */


.text-align-last-center {
    text-align-last: center;
}

.text-align-center {
    text-align: center;
}

.text-bold {
    font-weight: bold;
}

.display-flex {
    display: flex;
}

.margin-left-0 {
    margin-left : 0 !important;
}

