/* 트리 테이블 전체 css */

.bom-user-tree-table-div {
    font-size: 13px;
}

.bom-user-tree-table-div .rs-table-row-header .rs-table-cell {
    background: #f0f0ff;
}

.bom-user-tree-table-div .rs-table-hover .rs-table-row:not(.rs-table-row-header):hover,
.bom-user-tree-table-div .rs-table-hover .rs-table-row:hover .rs-table-cell-group,
.bom-user-tree-table-div .rs-table-hover .rs-table-row:hover .rs-table-cell {
  background: #f0f0f0;
}

.bom-user-tree-table-div .rs-table-word-wrap .rs-table-cell-content {
    white-space: normal;
    word-break:keep-all;
  }


/* 레벨에 따른 들여쓰기 10으로 강제 고정 */
.bom-user-tree-table-div .rs-table-cell-content {
    padding-left: 10px !important;
}


.bom-user-tree-table-div .bom-user-tree-table {
    margin: 0 auto;
    margin-top : 50px;
}

/* BOM Tree Table */

.bom-user-tree-table-div div[data-depth='0'] .bom-tree-row-1 {
    background-color: #d6d1fd;
}

.bom-user-tree-table-div div[data-depth='1'] .bom-tree-row-1 {
    background-color:#dceaf7;
}

.bom-user-tree-table-div div[data-depth='2'] .bom-tree-row-1 {
    background-color: rgb(186, 232, 253);
}

.bom-user-tree-table-div div[data-depth='3'] .bom-tree-row-1 {
    background-color: rgb(175, 198, 248);
}

.bom-user-tree-table-div div[data-depth='4'] .bom-tree-row-1 {
    background-color: rgb(200, 218, 255);
}

.bom-user-tree-table-div div[data-depth='5'] .bom-tree-row-1 {
    background-color: rgb(223, 242, 255);
}

.bom-user-tree-table-div div[data-depth='6'] .bom-tree-row-1 {
    background-color: rgb(220, 250, 255);
}


.bom-user-tree-table-div div[data-depth='0'] .bom-tree-row-2 {
    background-color: #d6d1fd;
}

.bom-user-tree-table-div div[data-depth='1'] .bom-tree-row-3 {
    background-color:#dceaf7;
}

.bom-user-tree-table-div div[data-depth='2'] .bom-tree-row-4 {
    background-color: rgb(186, 232, 253);
}

.bom-user-tree-table-div div[data-depth='3'] .bom-tree-row-5 {
    background-color: rgb(175, 198, 248);
}

.bom-user-tree-table-div div[data-depth='4'] .bom-tree-row-6 {
    background-color: rgb(200, 218, 255);
}

.bom-user-tree-table-div div[data-depth='5'] .bom-tree-row-7 {
    background-color: rgb(223, 242, 255);
}

.bom-user-tree-table-div div[data-depth='6'] .bom-tree-row-8 {
    background-color: rgb(220, 250, 255);
}

.bom-user-tree-table-div .bom-user-tree-table .rs-table-cell-header .rs-table-cell-content{
    font-weight: bold;
    font-size:14px;
    color:#7777D9;
}

.bom-user-tree-table-div .rs-table-cell-expand-wrapper .bom-tree-button {
    display: none;
}

.bom-user-tree-table-div div[data-depth='0'] .bom-tree-row-2 .bom-tree-button {
    display: block;
}

.bom-user-tree-table-div div[data-depth='1'] .bom-tree-row-3 .bom-tree-button {
