}

.bom-user-tree-table-div div[data-depth='2'] .bom-tree-row-4 .bom-tree-button {
    display: block;
}

.bom-user-tree-table-div div[data-depth='3'] .bom-tree-row-5 .bom-tree-button {
    display: block;
}

.bom-user-tree-table-div div[data-depth='4'] .bom-tree-row-6 .bom-tree-button {
    display: block;
}

