/* 사용자 BOM 입력 Step 2 전체 */

.bom-user-step2-all{
    margin-left:200px;
    margin-right:200px;
}





.bom-user-input-step2-flex {
    display: flex;
}


/* 사용자 BOM 입력 스텝1 가는 버튼 */

.bom-user-input2-arrow{
    border:1px solid #000000;
    background-color: #F0F0FF;
    width: 100px;
    line-height: 100px;
    text-align: center;
}

.bom-user-input2-arrow-box{
    display: flex;
    align-items: center;
    margin-left:130px;
    margin-bottom:130px;
    font-size: 22px;
}

/* 사용자 BOM 스텝2 장치 레벨 상단바 */

.bom-user-input2-content {
    margin-left : 120px;
}

.bom-user-input2-form{
    width:800px;
}

.bom-user-input2-form-fieldset{
    border: 2px groove black ;
    border-radius: 15px;
    padding:15px 30px 30px 20px;
    height:130px;
}

.bom-user-input2-form-legend{
    width:auto;
    margin-left: 20px;
    margin-bottom: 0;
    border-bottom: none;
}

.bom-user-input2-form-pre-div{
    height: 70px;

}

.bom-user-input2-form-pre-div > p {
    height: 60px;
    padding: 9.5px;
    margin: 0 0 10px;
    font-size: 13px;
    line-height: 1.42857143;
    color: #333;
    background-color: #f5f5f5;
    border: 1px solid #ccc;
    border-radius: 4px;
    word-wrap: break-word;
}



/* 사용자 BOM 스텝2 입력 창 */


.bom-user-input2-text-div1 {
    border: 1px solid black;
    margin-top: 20px;
    width:800px;
    height: 310px;
}

.bom-user-input2-text-box {
    margin: 15px 0 0 40px;
}

.bom-user-input2-text-box > span {
    margin-right:40px;
    font-weight: bold;
}

.bom-user-input2-text-box > input, .bom-user-input2-text-box > select {
    width:580px;
}

.bom-user-input2-text-div2 {
    border: 1px solid black;
    margin-top: 20px;
    width:800px;
    height: 150px;
}

.bom-user-input2-text-div3 {
    border: 1px solid black;
    margin-top: 20px;
    width:800px;
    height: 190px;
}

.bom-user-input2-button-div {
    margin-left: 320px;
    margin-top : 20px;
}

.bom-user-input2-button-div button {
    width:80px;
    height:40px;
    margin-right:50px;
    background-color: #fdfdff;
    border-radius: 10px;
    border: 2px groove #8f8f8f;
}

